<template>
  <section class="loginContainer">
    <div class="loginInner">
      <div class="login_header">
        <h2 class="login_logo"></h2>
        <div class="login_header_title">
          <a href="javascript:;" :class="{on: loginWay}" @click="loginWay = true"></a>
          <a href="javascript:;" :class="{on: !loginWay}" @click="loginWay = false"></a>
        </div>
      </div>
      <div class="login_content">
        <form>
          <div :class="{on: loginWay}">
            <section class="login_message">
              <input type="tel" maxlength="11" placeholder="" v-model="phone">
              <button :disabled="!isRightPhone" class="get_verification" :class="{right_phone: isRightPhone}"
                      @click.prevent="sendCode">{{computeTime>0?`(${computeTime}s)`:''}}
              </button>
            </section>
            <section class="login_verification">
              <input type="tel" maxlength="8" placeholder="" v-model="code">
            </section>
            <section class="login_hint">
              
              <a href="javascript:;"></a>
            </section>
          </div>
          <div :class="{on: !loginWay}">
            <section>
              <section class="login_message">
                <input type="tel" maxlength="11" placeholder="//" v-model="name">
              </section>
              <section class="login_verification">
                <input :type="isShowPwd?'text':'password'" maxlength="8" placeholder="" v-model="pwd">
                <div class="switch_button" :class="{on: isShowPwd}" @click="isShowPwd = !isShowPwd">
                  <div class="switch_circle" :class="{right: isShowPwd}"></div>
                  <span class="switch_text">abc</span>
                </div>
              </section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="" v-model="captcha">
                <img ref="captcha" class="get_verification" src="http://localhost:4000/captcha"
                     alt="captcha" @click="updateCaptacha">
              </section>
            </section>
          </div>
          <button class="login_submit" @click.prevent="login"></button>
        </form>
        <a href="javascript:;" class="about_us"></a>
      </div>
      <a href="javascript:" class="go_back" @click="$router.back()"> <!---->
        <i class="iconfont icon-jiantou2"></i>
      </a>
    </div>
  </section>
</template>
<script>
  import {Toast, MessageBox} from 'mint-ui';

  import {reqSendCode, reqSmsLogin, reqPwdLogin} from '../../api'

  export default {
    data() {
      return {
        loginWay: false, // true, false
        phone: '',  // 
        code: '', // 
        name: '', // 
        pwd: '',    // 
        captcha: '',  // 
        computeTime: 0, // 
        isShowPwd: true // 
      }
    },

    computed: {
      isRightPhone() {
        return /^1\d{10}$/.test(this.phone) // 111
      }
    },

    methods: {
      // 
      showAlert(msg) {
        MessageBox.alert(msg, '');
      },

      // 
      async login() {
        const {phone, code, name, pwd, captcha} = this
        let reslut
        // 1. 
        if (this.loginWay) { // 

          if (!this.isRightPhone) {
            // MessageBox.alert('', '');
            this.showAlert('')
            return
          } else if (!/^\d{6}$/.test(code)) {
            // MessageBox.alert('', '');
            this.showAlert('')
            return
          }
           // 2. ()
          reslut = await reqSmsLogin(phone, code)

        } else { //

          if (!name) {
            // MessageBox.alert('', '');
            this.showAlert('')
            return
          } else if (!pwd) {
            // MessageBox.alert('', '');
            this.showAlert('')
            return
          } else if (!captcha) {
            // MessageBox.alert('', '');
            this.showAlert('')
            return
          }
          // 2. ()
          reslut = await reqPwdLogin({name, pwd, captcha})
        }

        // 3. 
        if(reslut.code === 0){ // 
          const user = reslut.data;

          // userstate
          this.$store.dispatch('saveUser', user)
          // /profile
          this.$router.replace('/profile')

        }else{ // 
          this.showAlert(reslut.msg)

          // 
          this.computeTime = 0;

          // ()
          !this.loginWay && this.updateCaptacha()
        }
      },

      // 
      async sendCode(event) {
        if (this.isRightPhone) {
          //  1. 
          //  
          event.target.className = 'get_verification'
          this.computeTime = 10
          // 1s1
          const interId = setInterval(() => {
            this.computeTime--
            // 0
            if (this.computeTime <= 0) {
              this.computeTime = 0;
              clearInterval(interId)
              event.target.className = 'get_verification right_phone'
            }
          }, 1000)

          //2. ajax--> 
          const result = await reqSendCode(this.phone)

          if (result.code === 0) {
            // 
            Toast('');
          } else {
            // 
            this.computeTime = 0  // <=
            // 
            MessageBox.alert(result.msg, '');
          }
        }
      },

      // 
      updateCaptacha() {
        // event.target.src = 'http://localhost:4000/captcha'
        // srcsrc, 
        // , , 
        this.$refs.captcha.src = 'http://localhost:4000/captcha?time=' + Date.now()
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .loginContainer
    width 100%
    height 100%
    background #ffffff
    .loginInner
      padding-top 0.6rem
      width 80%
      margin 0 auto
      .login_header
        .login_logo
          font-size 0.4rem
          font-weight bold
          color #02a774
          text-align center
        .login_header_title
          padding-top 0.4rem
          text-align center
          > a
            color #333333
            font-size 0.14rem
            padding-bottom 0.04rem
            &:first-child
              margin-right 0.4rem
            &.on
              color #02a774
              font-weight 7rem //700
              border-bottom 0.02rem solid #02a774
      .login_content
        > form
          > div
            display none
            &.on
              display block
            input
              width 100%
              height 100%
              padding-left 0.1rem
              box-sizing border-box
              border 0.01rem solid #dddddd
              border-radius 0.04rem
              outline 0
              font 4rem 0.14rem Arial
              &:focus
                border 0.01rem solid #02a774
            .login_message
              position relative
              margin-top 0.16rem
              height 0.48rem
              font-size 0.14rem
              background #ffffff
              .get_verification
                position absolute
                top 50%
                right 0.1rem
                transform translateY(-50%)
                border 0
                color #cccccc
                font-size 0.14rem
                background transparent
                &.right_phone
                  color #000000

            .login_verification
              position relative
              margin-top 0.16rem
              height 0.48rem
              font-size 0.14rem
              background #ffffff
              .switch_button
                font-size 0.12rem
                border 0.01rem solid #dddddd
                border-radius 0.08rem
                transition background-color .3, border-color .3s
                padding 0 0.06rem
                width 0.3rem
                height 0.16rem
                line-height 0.16rem
                color #ffffff
                position absolute
                top 50%
                right 0.1rem
                transform translateY(-50%)
                &.off
                  background #ffffff
                  .switch_text
                    float right
                    color #dddddd
                &.on
                  background #02a774
                > .switch_circle
                  position absolute
                  top -0.01rem
                  left -0.01rem
                  width 0.16rem
                  height 0.16rem
                  border 0.01rem solid #dddddd
                  border-radius 50%
                  background #ffffff
                  box-shadow 0 0.02rem 0.04rem 0 rgba(0, 0, 0, .1)
                  transiton transform .4s
                  &.right
                    transform translateX(0.26rem)
            .login_hint
              margin-top 0.12rem
              color #999999
              font-size 0.14rem
              line-height 0.2rem
              > a
                color #02a774
          .login_submit
            display block
            width 100%
            height 0.42rem
            margin-top 0.3rem
            border-radius 0.04rem
            background #4cd96f
            color #ffffff
            text-algin center
            font-size 0.16rem
            line-height 0.42rem
            border 0
        .about_us
          display block
          font-size 0.12rem
          margin-top 0.2rem
          text-align center
          color #999999
      .go_back
        position absolute
        top 0.05rem
        left 0.05rem
        width 0.3rem
        height 0.3rem
        > .iconfont
          font-size 0.2rem
          color #999999
</style>



