/*
vuexmutations
mutationaction: commit('mutationName')
 */
import Vue from 'vue';

import {
  RECEIVE_ADDRESS,
  RECEIVE_CATEGORYS,
  RECEIVE_SHOPS,
  RECEIVE_USER,
  RESET_USER,
  RECEIVE_GOODS,
  RECEIVE_RATINGS,
  RECEIVE_INFO,
  INCREASE_FOOD_COUNT,
  DECREASE_FOOD_COUNT
} from './mutation-types'

export default {
  // address
  [RECEIVE_ADDRESS](state, {address}){
    // stateaddress
    state.address = address
  },
  [RECEIVE_CATEGORYS](state, {categorys}){
    state.categorys = categorys
  },
  [RECEIVE_SHOPS](state, {shops}){
    state.shops = shops
  },
  [RECEIVE_USER](state, {user}){
    state.user = user
  },
  [RESET_USER](state){
    state.user = {}
  },
  [RECEIVE_GOODS](state, {goods}){
    state.goods = goods
  },
  [RECEIVE_RATINGS](state, {ratings}){
    state.ratings = ratings
  },
  [RECEIVE_INFO](state, {info}){
    state.info = info
  },

  [INCREASE_FOOD_COUNT](state, {food}){
    if(food.count){
      food.count++
    }else{
      //  count 1
      // food.count = 1 // 
      Vue.set(food,'count',1) // 

      // foodcartFood
      state.cartFoods.push(food)
    }
  },
  [DECREASE_FOOD_COUNT](state, {food}){
    if(food.count){ //  count > 0 1
      food.count--
      // 0 food  cartFoods 
      if(food.count === 0){
        state.cartFoods.splice(state.cartFoods.indexOf(food), 1)
      }
    }
  }
  ,
}



